body {
  color:white;
  background-color: #333;
}

.dual-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 720px;
  margin: auto;
  justify-items: center;
  align-items: center;
}

.time-sets {
  display: flex;
  grid-template-columns: 50px 200px 50px;
  justify-items: center;
  align-items: center;
}